import React, { useEffect, useState } from "react";
import { Button, Card, Form, Col, Row, Alert } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { withRouter } from "react-router-dom";
import { toast } from "react-toastify";
import { getAutoCompleteAddressAPI, loginCustomerAPI } from "../api/authApi";
import Loader from "./Loader";
import { getStoreQuotesUpdateCustomerId } from "../api/quoteApi";
import { checkValidEmail } from "../common/utils";
import { setPageLoader, setQuoteDetails } from "../redux/Actions/authAction";
import { updateQuote } from "../api/updateQuote";
import { Link } from "react-router-dom";
// import Main from "./Main";

const Login = (props) => {
  const dispatch = useDispatch();
  const usertoken = sessionStorage.getItem("token");
  const quoteDetail = useSelector((state) => state.auth.quoteDetail);
  const [loading, setLoading] = useState(false);
  const [formState, setFormState] = useState({
    email: "",
    password: "",
  });
  const [error, setError] = useState({
    email: "",
    password: "",
  });

  const onChange = (e, type) => {
    const { value } = e.target;
    setFormState({
      ...formState,
      [type]: value,
    });
  };

  const validate = () => {
    const { email, password } = formState;
    setError({
      ...error,
      email: !email
        ? "Email is required!"
        : !checkValidEmail(email)
        ? "Email is invalid!"
        : "",
      password: !password ? "Password is required!" : "",
    });
    if (email && password && !error.email && !error.password) {
      return true;
    } else {
      return false;
    }
  };

  const handleSubmit = () => {
    const customerIdList =
      JSON.parse(sessionStorage.getItem("customer_ids")) || [];
    const customerIds = customerIdList.length
      ? customerIdList.map((it) => {
          return { id: it };
        })
      : [];
    if (validate()) {
      setLoading(true);
      dispatch(setPageLoader(true));
      loginCustomerAPI(formState)
        .then((response) => {
          if (response && response.data) {
            sessionStorage.setItem("token", response.data.data.token);
            dispatch(setPageLoader(false));
            // dispatch(setQuoteDetails({}));
            setLoading(false);
            if (quoteDetail.from_address) {
              props.history.push("/quotes");
            } else {
              props.history.push("/dashboard");
            }
            // if(customerIds && customerIds.length){
            //   getStoreQuotesUpdateCustomerId(customerIds).then(response => {
            //     console.log(response.data, "getStoreQuotesUpdateCustomerId");
            //     props.history.push('/payment');
            //   })
            // }
            // toast.success(response.data.message, {
            //   theme: "colored",
            // });
          }
        })
        .catch((err) => {
          setLoading(false);
          toast.error(err.response.data.message, {
            theme: "colored",
          });
        });
    }
  };

  const gotoPasswords = () => {
    props.history.push("/forgot-password");
  };

  const gotoSignUP = () => {
    props.history.push("/registration");
  };

  const handleCheck = (e) => {
    const data = {
      email: formState.email,
      password: formState.password,
    };
    if (e.target.checked) {
      sessionStorage.setItem("user", JSON.stringify(data));
    } else {
      sessionStorage.setItem("user", "");
    }
  };

  

  const getMap = () => {
    getAutoCompleteAddressAPI();
  };
  useEffect(() => {
    if(sessionStorage.getItem("user")){
      formState.email=JSON.parse(sessionStorage.getItem("user")).email
      formState.password=JSON.parse(sessionStorage.getItem("user")).password  
      setFormState(formState)
    }
  }, [])
  // shadow bg-white rounded-top
  return (
    <div>
        <div className="loginpage-section">
          <Card className="container">
            <Card.Body>
              <div className="text-center login-box">
                <div className="login-text">
                  <Card.Title>
                    <span className="title-thin">{"USER "}</span>
                    <span className="">{"LOGIN"}</span>
                  </Card.Title>
                  <Card.Text>
                    We'd love to discuss our flexible delivery solutions with
                    you! provide your contact information and we'll reach out to
                    you!
                  </Card.Text>
                </div>
                <Form>
                  <Form.Group as={Row} controlId="formHorizontalEmail">
                    <Form.Label column sm={2} className="text-left text-nowrap">
                      Email
                    </Form.Label>
                    <Col sm={10}>
                      <Form.Control
                        className={
                          error && error["email"] ? "error-border" : ""
                        }
                        type="text"
                        placeholder="Email"
                        defaultValue={JSON.parse(sessionStorage.getItem("user"))?.email}
                        onChange={(e) => onChange(e, "email")}
                      />
                      {error && error["email"] ? (
                        <Alert className="mt-2 p-0" variant={"danger"}>
                          {error["email"]}
                        </Alert>
                      ) : (
                        ""
                      )}
                    </Col>
                  </Form.Group>
                  <Form.Group as={Row} controlId="formHorizontalEmail">
                    <Form.Label column sm={2} className="text-left text-nowrap">
                      Password
                    </Form.Label>
                    <Col sm={10}>
                      <Form.Control
                        className={
                          error && error["password"] ? "error-border" : ""
                        }
                        type="password"
                        placeholder="Password"
                        defaultValue={JSON.parse(sessionStorage.getItem("user"))?.password}
                        onChange={(e) => onChange(e, "password")}
                      />
                      {error && error["password"] ? (
                        <Alert className="mt-2 p-0" variant={"danger"}>
                          {error["password"]}
                        </Alert>
                      ) : (
                        ""
                      )}
                    </Col>
                  </Form.Group>
                  <Form.Group as={Row}>
                    <Col sm={{ span: 10, offset: 2 }}>
                      <Row className="">
                        <Col sm={6} className="text-left">
                          <div class="form-check ">
                            <input
                              className="form-check-input"
                              type="checkbox"
                              value=""
                              id="invalidCheck"
                              onClick={handleCheck}
                              disabled={
                                formState.email && formState.password
                                  ? false
                                  : true
                              }
                            />
                            <label
                              className="form-check-label text-nowrap"
                              for="invalidCheck"
                            >
                              Remember me
                            </label>
                          </div>
                        </Col>
                        <Col sm={6} className="text-right">
                        <Link to="/forgot-password">
                          <span className="cursor-pointer" onClick={getMap}>
                            Forgot password
                          </span>
                          </Link>
                        </Col>
                      </Row>
                    </Col>
                  </Form.Group>
                  <Form.Group as={Row} className="mt-4">
                    <Col sm={{ span: 10, offset: 2 }}>
                      <div className="doyou-account">
                        Don't have an account?{" "}
                        <span className="cursor-pointer" onClick={gotoSignUP}>
                          Register
                        </span>
                      </div>
                    </Col>
                  </Form.Group>
                  <Form.Group as={Row}>
                    <Col sm={{ span: 10, offset: 2 }}>
                      {!loading && (
                        <Button
                          type="button"
                          className="w-100 theme-blue"
                          onClick={handleSubmit}
                        >
                          Login
                        </Button>
                      )}
                      {loading && (
                        <Button type="button" className="w-100 theme-blue">
                          Login...
                        </Button>
                      )}
                    </Col>
                  </Form.Group>
                </Form>
              </div>
            </Card.Body>
          </Card>
        </div>
        {
          loading && <Loader/>
        }
    </div>
  );
};

export default withRouter(Login);
