import React,{useState} from "react";
import "../../pages/PaymentPage/UpdateInfo.css";
import { Form, Input, Button,Row, Col,Select } from "antd";
import { changePassword } from "../../api/userUpdate";
import Loader from "../../pages/Loader"
import { useHistory } from "react-router";



const ChangePassword = ({setMessage , setType}) => {
  const [loading, setLoading] = useState(false);
  const history = useHistory()
  const onFinish = (values) => {
    changePassword(values , setLoading , history,setType,setMessage)
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };

  return (
    <div>
        <Row>
          <Col md={24} lg={24} sm={24}>
          
            <Form
              className="mt-3"
              name="basic"
              initialValues={{
                remember: true,
              }}
              onFinish={onFinish}
              onFinishFailed={onFinishFailed}
              autoComplete="off"
            >
              <Form.Item
                labelCol={{ span: 8 }} wrapperCol={{ span: 20 }}
                label="Current Password:"
                name="old_password"
                rules={[
                  {
                    required: true,
                    message: "Please input your old password!",
                  },
                ]}
              >
                <Input.Password placeholder="Enter your old password" />
              </Form.Item>
              <Form.Item
                label="New Password"
                name="new_password"
                rules={[
                  {
                    required: true,
                    message: "Please input your new password!",
                  },
                ]}
              >
                <Input.Password placeholder="Enter new password" />
              </Form.Item>
              <Form.Item
                label="Confirm Password"
                name="confirm_password"
                rules={[
                  {
                    required: true,
                    message: "Please input your retype_password!",
                  },
                ]}
              >
                <Input.Password placeholder="Re-type new password" />
              </Form.Item>
              <Form.Item
                wrapperCol={{
                  offset: 6,
                  span: 16,
                }}
              >
                <Button htmlType="submit" className="w-100 light-blue-bg">
                  Update Password
                </Button>
              </Form.Item>
            </Form>
          </Col>
         
        </Row>
        {
          loading && <Loader/>
        }
    </div>
  );
};
export default ChangePassword;
