import React, { useState, useEffect } from "react";
import { Card, Button } from "react-bootstrap";
import Box from "../assets/images-_1_.png";
import { Link } from "react-router-dom";
import { All_Order_Listing } from "../api/Order/OrderListing";
import Loader from "../pages/Loader";
import Alertify from "../pages/Alertify";
import moment from "moment";
import { handleBreakpoints } from "@mui/system";
import { DatePicker, Space } from 'antd';
import { EyeOutlined } from '@ant-design/icons';

const OrderHistory = () => {

  const { RangePicker } = DatePicker;
  const [type, setType] = useState("")
  const [message, setMessage] = useState("")
  const [data, setData] = useState([]);
  const [loader, setLoader] = useState(false);
  const [loaders, setLoaders] = useState(false);
  const [searchId, setSearchId] = useState("")
  const [loadmore, setLoadmore] = useState(5);
  const [getRange , setGetRange] = useState({
    from:"",
    to:""
  })

  const getOrder = (e) => {
    e.preventDefault()
    All_Order_Listing(setData, setLoader, searchId, getRange);
  }

  const ids = data.map(o => o.order_id)
  const filtered = data.filter(({ order_id }, index) => !ids.includes(order_id, index + 1))

  const handleChange = (e) => {
    setSearchId(e.target.value)
  }

  const loadmore_function = () => {
    setLoadmore(loadmore + 5)
  }

  const Range_Picker=(e)=>{
    getRange.from=moment(e[0]).format("DD-MM-YYYY")
    getRange.to=moment(e[1]).format("DD-MM-YYYY")
    setGetRange(getRange)
  }


  useEffect(() => {
    All_Order_Listing(setData, setLoader, searchId);
    if (sessionStorage.getItem("last_route") === "/current-order") {
      setType("success")
      setMessage("Your order has been confirmed, we have sent a confirmation email to your registered email")

    }
  }, []);

  return (
    <div className="Orderpage-section">
      <div className="container">
        {sessionStorage.getItem("last_route") === "/current-order" && (type === "error" || type === "success") && <Alertify message={message} type={type} setType={setType} />}
        <Card className="heading">
          <Card.Body>
            <div className="orderpage-upper">
              <Card.Title className="register-title">
                {window.location.pathname === "/order-history" ? <div><span className="title-thin">ORDER</span> HISTORY</div> : <div><span className="title-thin">MY</span> ORDER</div>}
              </Card.Title>
              <Card.Text>
                It is a long established fact that a reader will be distracted
                by the readable content of a page when looking at its layout.
              </Card.Text>

              <form className="d-flex">
                  <RangePicker className="ordpageinput form-control" style={{ marginRight: "5px" }} onChange={Range_Picker} format="DD-MM-YYYY" />
                <input
                  class="form-control"
                  type="search"
                  placeholder="Search your oder here"
                  aria-label="Search"
                  onChange={handleChange}
                />
                <button className="oderListBtn" onClick={getOrder} type="submit">
                  SEARCH ODER
                </button>
              </form>
            </div>

            {!loader &&
              filtered.length >= 1 ?
              <>
                {
                  filtered?.slice(0, loadmore).map((e, i) => (

                    <div className="box">
                      <div className="table-responsive" key={`order${i}`}>
                        <table>
                          <tbody>
                            {
                              <tr>
                                <td>
                                  <div className="image">
                                    <img src={Box} width="80" height="75" />
                                  </div>
                                </td>
                                <td>
                                  <div className="ordr-detl">
                                    <p>
                                      <span>Order Date:</span> {moment(e?.created_at).format("MMM DD, YYYY")}
                                    </p>
                                    <p>
                                      <span>Order Time:</span> {moment(e?.created_at).format("HH:mm")}
                                    </p>
                                    <p>
                                      <span>Order Id:</span> {e?.order_id}
                                    </p>
                                    <p>
                                      <span>Shipper:</span> Trans8
                                    </p>
                                    <p>
                                      <span>Weight:</span> {e?.dimension_weight}Kg .
                                    </p>
                                  </div>
                                </td>
                                <td align="right">
                                  <div className="order-dte">
                                    <p>
                                      Delivery date <br />
                                      {moment(e?.delivery_date, 'YYYY-MM-DD').format('MMM DD , YYYY')}
                                    </p>
                                  </div>
                                </td>
                              </tr>
                            }
                            <tr>
                              <td colSpan="1"></td>
                              <td>
                                <div className="odr-detail">
                                  <Link to={`/order-details/${e.order_id}`} >
                                    <EyeOutlined />
                                    View order details

                                  </Link>
                                  {loaders && <Loader />}
                                </div>
                              </td>
                              <td align="right">
                                <div className="odr-sttus sts-green">
                                  {e?.status === "active" && e?.oder_delivered === "not-delivered" && "Order not delivered"}
                                  {e?.status === "active" && e?.oder_delivered === "delivered" && "Order delivered"}
                                  {e?.status === "inactive" && "Order not confirmed"}
                                  {e?.status === "cancel" && "Order Cancelled"}
                                </div>
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  ))}
                {loadmore < filtered.length && <Button className="btn btn-info mt-3 lodmr-btn" onClick={loadmore_function}>Load more</Button>}
              </>
              : (searchId === "" ? <h6 style={{ color: '#707070' }}>Sorry, you have booked no freight yet</h6> : <h5 style={{ color: '#707070' }}>Sorry no order found</h5>)}
            {loader && <Loader />}
          </Card.Body>
        </Card>
      </div>
    </div>
  );
};

export default OrderHistory;

// for unique check from objects
