import React from 'react'
import {Doughnut} from 'react-chartjs-2'
import moment from 'moment'
function PieChart({graphData}) {
  let dataSet=graphData.map(e=>e.total)
    let months=graphData.map(e=>moment(e.month , "MM").format("MMM"))
    return (
        <div>
            <Doughnut
                data={{
                    // labels: months,
                    labels: ["jan", "feb ", "march", "april", "may" , "june"],
                    datasets: [
                       {
                        //  data: dataSet,
                        data: [0, 10, 5, 4, 8, 3],
                         backgroundColor: [
                           "red", "green" , "blue" , "yellow" , "pink ", "orange"
                          ], label: 'Doughnut 1'
                        }],
                        borderWidth: 10 
                }}
            />
        </div>
    )} 

  export default PieChart  