import axios from 'axios'
import { toast } from "react-toastify";
// import { getAllUserDetail } from '../redux/Actions/authAction';
let api_url = "http://devapi.trans8.ca/";


// user oder listing details
export const All_Order_Listing = (setData, setLoader, searchId , getRange) => {
    console.log(getRange)
    setLoader(true);
    let val = {}
    if (searchId === "") {
        val = {}
    } else {
        val = {
            order_id: [searchId],
        }
    }
    axios.post(api_url + "api/getallorders", val, { headers: { 'Authorization': 'Bearer ' + sessionStorage.getItem("token") } })
        .then(response => {
            setData(response.data.data)
            setLoader(false)
        })
        .catch(err => {
            setLoader(false)
        })
}

//All active order
export const active_order_Listing = (setData, setLoader, searchId) => {
    setLoader(true);
    let val = {}
    if (searchId === "") {
        val = {}
    } else {
        val = {
            order_id: [searchId]
        }
    }
    axios.post(api_url + "api/getallactiveorders", val, { headers: { 'Authorization': 'Bearer ' + sessionStorage.getItem("token") } })
        .then(response => {
            setData(response.data.data)
            setLoader(false)
        })
        .catch(err => {
            toast.error("Something went wrong!", {
                theme: "colored", position: 'top-center',
            });
            setLoader(false)
        })
}

