//  api/confirmationstatus
export const confirm_status = (data, setLoader, history, status) => {
    setLoader(true);
    axios.post(api_url + "api/confirmationstatus", data, { headers: { 'Authorization': 'Bearer ' + sessionStorage.getItem("token") } })
        .then(response => {
            console.log(response);
            if (status === "active") {
                sessionStorage.setItem("last_route", window.location.pathname)
                // setType("success")
                // setMessage("Your order has been confirmed, we have sent a confirmation email to your registered email")
                // toast.success("Your order has been confirmed, we have sent a confirmation email to your registered email", {
                //     theme: "colored",position: 'top-center',
                //   });
                history.push("/my-order")
            }
            else {
                // setType("success")
                // setMessage("Your order has been cancelled")
                // toast.success("Your order has been cancelled", {
                //     theme: "colored",position: 'top-center',
                //   });
                history.push("/dashboard")
            }
            // setData(response.data.data)
            setLoader(false)
        })
        .catch(err => {
            // setType("error")
            // setMessage("Something went wrong!")
            // toast.error("Something went wrong!", {
            //     theme: "colored",position: 'top-center',
            //   });
            setLoader(false)
        })
}

