import React, { useEffect, useState } from "react";
import { Button, Card, Form, Col, Row, Alert } from "react-bootstrap";
import { registerCustomerAPI } from "../api/authApi";
import { withRouter } from "react-router-dom";
import { toast } from "react-toastify";
// import { useHistory } from "react-router";
import { checkValidEmail, isNumber, isString } from "../common/utils";
import ScrollToBottom from "react-scroll-to-bottom";
import { css } from "@emotion/css";
import Loader from "./Loader";
import Alertify from "./Alertify";

const ROOT_CSS = css({
  height: "-400px",
  width: "100%",
});

const Registration = (props) => {
  const [type , setType]= useState("")
  const [message , setMessage]= useState("")
  const [loading, setLoading] = useState(false);
  // const history= useHistory()
  const [formState, setFormState] = useState({
    first_name: "",
    last_name: "",
    email: "",
    phone_number: "",
    password: "",
    c_password: "",
  });

  const [error, setError] = useState({
    first_name: "",
    last_name: "",
    email: "",
    phone_number: "",
    password: "",
    c_password: "",
  });

  const onChange = (e, type) => {
    const { value } = e.target;
    setFormState({
      ...formState,
      [type]: value,
    });
  };

  const validate = () => {
    const { first_name, last_name, email, phone_number, password, c_password } =
      formState;
    setError({
      ...error,
      first_name: !first_name
        ? "First name is required!"
        : !isString(first_name)
        ? "First name should be text only!"
        : "",
      last_name: !last_name
        ? "Last name is required!"
        : !isString(first_name)
        ? "Last name should be text only!"
        : "",
      email: !email
        ? "Email is required!"
        : !checkValidEmail(email)
        ? "Email is invalid!"
        : "",
      phone_number: !phone_number
        ? "Phone number is required!"
        : !isNumber(phone_number)
        ? "Phone number should be number!"
        : phone_number.toString().length === 10
        ? ""
        : "Phone number must be 10 digits!",
      password: !password ? "Password is required!" : "",
      c_password: !c_password
        ? "Please confirm your password!"
        : password !== c_password
        ? "Password does not match!"
        : "",
    });
    if (
      first_name &&
      last_name &&
      email &&
      phone_number &&
      password &&
      c_password &&
      !error.first_name &&
      !error.last_name &&
      !error.email &&
      !error.phone_number &&
      !error.password &&
      !error.c_password
    ) {
      return true;
    } else {
      return false;
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) {
      setLoading(true);
      registerCustomerAPI(formState)
        .then((response) => {
          if (response && response.data) {
            sessionStorage.setItem("token", response.data.data.token);
            setType("success")
            setMessage("Customer register successfully")
            // toast.success("Customer register successfully", {
            //   theme: "colored",
            // });
            setLoading(false);
            setTimeout(() => {
              props.history.push("/");
            }, 1000);
          }
        })
        .catch((err) => {
          setLoading(false);
          setType("error")
          setMessage(err.response.data.message)
          // toast.error(err.response.data.message, {
          //   theme: "colored",
          // });
        });
    }
  };

  const gotoLogin = () => {
    props.history.push("/login");
  };

  useEffect(() => {
    const usertoken = sessionStorage.getItem("token");
    if (usertoken) {
      props.history.push("/");
    }
  }, []);

  return (
    <div>
    
        <ScrollToBottom className={ROOT_CSS}>
          <div className="loginpage-section">
            <Card className="container">
            {(type==="error" || type==="success") && <Alertify  message={message} type={type}  setType={setType} />}
              <Card.Body>
                <div className="text-center login-box">
                  <div className="login-text">
                    <Card.Title>
                      <span className="title-thin">{"LOGIN "}</span>
                      <span className="">REGISTRATION</span>
                    </Card.Title>
                    <Card.Text>
                      We'd love to discuss our flexible delivery solutions with
                      you! provide your contact information and we'll reach out
                      to you!
                    </Card.Text>
                  </div>

                  <Form>
                    <Form.Group as={Row} controlId="formHorizontalEmail">
                      <Form.Label
                        column
                        sm={2}
                        className="text-left text-nowrap"
                      >
                        First Name
                      </Form.Label>
                      <Col sm={10}>
                        <Form.Control
                          className={
                            error && error["first_name"] ? "error-border" : ""
                          }
                          type="text"
                          placeholder="First Name"
                          defaultValue={formState && formState["first_name"]}
                          onChange={(e) => onChange(e, "first_name")}
                        />
                        {error && error["first_name"] ? (
                          <Alert className="mt-2 p-0" variant={"danger"}>
                            {error["first_name"]}
                          </Alert>
                        ) : (
                          ""
                        )}
                      </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="formHorizontalEmail">
                      <Form.Label
                        column
                        sm={2}
                        className="text-left text-nowrap"
                      >
                        Last Name
                      </Form.Label>
                      <Col sm={10}>
                        <Form.Control
                          className={
                            error && error["last_name"] ? "error-border" : ""
                          }
                          type="text"
                          placeholder="Last Name"
                          defaultValue={formState && formState["last_name"]}
                          onChange={(e) => onChange(e, "last_name")}
                        />
                        {error && error["last_name"] ? (
                          <Alert className="mt-2 p-0" variant={"danger"}>
                            {error["last_name"]}
                          </Alert>
                        ) : (
                          ""
                        )}
                      </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="formHorizontalEmail">
                      <Form.Label
                        column
                        sm={2}
                        className="text-left text-nowrap"
                      >
                        Email
                      </Form.Label>
                      <Col sm={10}>
                        <Form.Control
                          className={
                            error && error["email"] ? "error-border" : ""
                          }
                          type="email"
                          placeholder="Email"
                          defaultValue={formState && formState["email"]}
                          onChange={(e) => onChange(e, "email")}
                        />
                        {error && error["email"] ? (
                          <Alert className="mt-2 p-0" variant={"danger"}>
                            {error["email"]}
                          </Alert>
                        ) : (
                          ""
                        )}
                      </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="formHorizontalEmail">
                      <Form.Label
                        column
                        sm={2}
                        className="text-left text-nowrap"
                      >
                        Phone
                      </Form.Label>
                      <Col sm={10}>
                        <Form.Control
                          className={
                            error && error["phone_number"] ? "error-border" : ""
                          }
                          type="text"
                          placeholder="Phone"
                          defaultValue={formState && formState["phone_number"]}
                          onChange={(e) => onChange(e, "phone_number")}
                        />
                        {error && error["phone_number"] ? (
                          <Alert className="mt-2 p-0" variant={"danger"}>
                            {error["phone_number"]}
                          </Alert>
                        ) : (
                          ""
                        )}
                      </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="formHorizontalEmail">
                      <Form.Label
                        column
                        sm={2}
                        className="text-left text-nowrap"
                      >
                        Password
                      </Form.Label>
                      <Col sm={10}>
                        <Form.Control
                          className={
                            error && error["password"] ? "error-border" : ""
                          }
                          type="password"
                          placeholder="Enter password"
                          defaultValue={formState && formState["password"]}
                          onChange={(e) => onChange(e, "password")}
                        />
                        {error && error["password"] ? (
                          <Alert className="mt-2 p-0" variant={"danger"}>
                            {error["password"]}
                          </Alert>
                        ) : (
                          ""
                        )}
                      </Col>
                    </Form.Group>
                    <Form.Group as={Row} controlId="formHorizontalEmail">
                      <Form.Label column sm={2} className="text-left ">
                        <span className="text-nowrap">Confirm</span>{" "}
                        <span className="text-nowrap">Password</span>
                      </Form.Label>
                      <Col sm={10}>
                        <Form.Control
                          className={
                            error && error["c_password"] ? "error-border" : ""
                          }
                          type="password"
                          placeholder="Confirm password"
                          defaultValue={formState && formState["c_password"]}
                          onChange={(e) => onChange(e, "c_password")}
                        />
                        {error && error["c_password"] ? (
                          <Alert className="mt-2 p-0" variant={"danger"}>
                            {error["c_password"]}
                          </Alert>
                        ) : (
                          ""
                        )}
                      </Col>
                    </Form.Group>
                    <Form.Group as={Row}>
                      <Col sm={{ span: 10, offset: 2 }}>
                        <div className="doyou-account">
                          Already a member?{" "}
                          <span
                            className="cursor-pointer"
                            onClick={() => gotoLogin()}
                          >
                            Login
                          </span>
                        </div>
                      </Col>
                    </Form.Group>
                    <Form.Group as={Row}>
                      <Col sm={{ span: 10, offset: 2 }}>
                        {!loading && (
                          <Button
                            type="button"
                            variant="info"
                            className="w-100 theme-blue"
                            onClick={handleSubmit}
                          >
                            REGISTER
                          </Button>
                        )}
                        {loading && (
                          <Button
                            type="button"
                            variant="info"
                            className="w-100 theme-blue"
                            onClick={handleSubmit}
                          >
                            Sign Up...
                          </Button>
                        )}
                      </Col>
                    </Form.Group>
                  </Form>
                </div>
              </Card.Body>
            </Card>
          </div>
        </ScrollToBottom>
        {
          loading && <Loader/>
        }
    </div>
  );
};

export default withRouter(Registration);
