import React ,{useState} from "react";
import {  Row, Col , InputNumber } from "antd";
import '../../App.css'
import Loader from "../Loader";
import { Form, Input, Button , Select } from "antd";
import { update_Info } from "../../api/userUpdate";
import { useSelector , useDispatch } from "react-redux";
import Alertify from "../../pages/Alertify";

const { Option } = Select;

const UpdateInfo = ({setMessage , setType}) => {
  const [loading, setLoading] = useState(false);
  const selector=useSelector(state=>state.auth.userDetail)
  const dispatch=useDispatch()
  const onFinish = (values) => {
    update_Info(values,setLoading ,dispatch , setType , setMessage )
  };

  return (
    <div>
        <Row>
          <Col md={24} lg={24} sm={24}>
            <Form
              className="mt-3"
              name="basic"
              onFinish={onFinish}
              autoComplete="off"
              labelCol={{ span: 12 }}
                WrapperCol={{span:4}}
                fields={
                  [
                    {
                      name:"first_name",
                      value:selector?.first_name
                    },
                    {
                      name:"last_name",
                      value:selector?.last_name
                    },
                    {
                      name:"phone_number",
                      value:selector?.phone_number
                    },
                    {
                      name:"email",
                      value:selector?.email
                    },{
                      name:"address",
                      value:selector?.address
                    },
                    {
                      name:"city",
                      value:selector?.city
                    },
                    {
                      name:"province",
                      value:selector?.province
                    },
                    {
                      name:"country",
                      value:selector?.country
                    },
                  ]
                }
            >
              <Form.Item
                label="First name"
                name="first_name"
                rules={[
                  {
                    required: true,
                    message: "Please input your username!",
                  },
                ]}
              >
                <Input placeholder="First name" />
              </Form.Item>
              <Form.Item
                label="Last name"
                name="last_name"
                rules={[
                  {
                    required: true,
                    message: "Please input your last name!",
                  },
                ]}
              >
                <Input placeholder="Last name" />
              </Form.Item>

              <Form.Item
                label="Phone"
                name="phone_number"
                rules={[
                  {
                    required: true,
                    message: "Please enter your phone number!",
                  },
                ]}
              >
                <InputNumber placeholder="Your phone" />
              </Form.Item>
              <Form.Item
                label="Email"
                name="email"
                // rules={[
                //   {
                //     required: true,
                //     message: "Please input your email!",
                //   },
                // ]}
              >
                <Input placeholder="Your email" readOnly/>
              </Form.Item>
              <Form.Item
                label="Address"
                name="address"
                rules={[
                  {
                    required: true,
                    message: "Please input your address!",
                  },
                ]}
              >
                <Input placeholder="Your address" />
              </Form.Item>
              <Form.Item
                label="City"
                name="city"
                rules={[
                  {
                    required: true,
                    message: "Please input your city!",
                  },
                ]}
              >
                <Input placeholder="Your city" />
              </Form.Item>
              <Form.Item
                label="Province"
                name="province"
                rules={[
                  {
                    required: true,
                    message: "Please enter your province!",
                  },
                ]}
              >
                <Input placeholder="Your province" />
              </Form.Item>
              <Form.Item
                label="Postal Code"
                name="postal_code"
                // rules={[
                //   {
                //     required: true,
                //     message: "Please input your postal Code",
                //   },
                // ]}
              >
                <Input placeholder="Your Postal Code" />
              </Form.Item>
              <Form.Item
                label="Country"
                name="country"
                rules={[
                  {
                    required: true,
                    message: "Please select your country!",
                  },
                ]}
              >
                <Select >
                  {/* <Option value="1">Country</Option> */}
                  <Option value="uae">UAE</Option>
                  <Option value="us">US</Option>
                  <Option value="uk">UK</Option>
                </Select>
              </Form.Item>
              <Form.Item
                wrapperCol={{
                  offset: 6,
                  span: 16,
                }}
              >
                <Button htmlType="submit" className="w-100 light-blue-bg">
                  Update Information
                </Button>
              </Form.Item>
            </Form>
          </Col>
        </Row>
        {
          loading && <Loader/>
        }
    </div>
  );
};
export default UpdateInfo;


