import axios from 'axios'
import { message } from 'antd'
import { toast } from "react-toastify";
import { get_card_list } from './userUpdate';
let api_url = "http://devapi.trans8.ca/";

// update store quote id
export const updateQuote = () => {
    let cust_id =JSON.parse(sessionStorage.getItem("customer_ids"))
    console.log("cust id" ,cust_id)
    let obj = { "data": [] }
    if (sessionStorage.getItem("customer_ids")) {
        for (var i = 0; i < cust_id.length; i++) {
            obj.data.push({ id: cust_id[i] })
        }
    }
        axios.post(api_url + "api/storequotesupdatecustomerid", obj, { headers: { 'Authorization': 'Bearer ' + sessionStorage.getItem("token") } })
            .then(response => {
                console.log(response)
            })
            .catch(err => {
                // toast.error("Something went wrong", {
                //     theme: "colored",position: 'top-center',
                // });
            })
    }


// updated card
export const update_card = (values,setLoading , dispatch , history , selector , quoteDetail ,setType, setMessage) => {
    setLoading(true);
    axios.post(api_url + "api/storepayment", values, { headers: { 'Authorization': 'Bearer ' + sessionStorage.getItem("token") } })
        .then(response => {
            setType("success")
            setMessage("Card detail saved successfully")
            // toast.success("Card detail saved successfully ", {
            //     theme: "colored",position: 'top-center',
            // });
            console.log("history" , history)
            if(sessionStorage.getItem("last_route")==="/quotes" ){
                history.push("/current-order")
            }
            get_card_list(dispatch , setLoading)
            setLoading(false)
        })
        .catch(err => {
            setLoading(false)
            // toast.error("Something went wrong", {
            //     theme: "colored",position: 'top-center',
            // });
        })
}

//Store quotes api
export const storeQuotes=(values)=>{
    axios.post(api_url+"api/storequotes" , values , { headers: { 'Authorization': 'Bearer ' + sessionStorage.getItem("token") } })
    .then(res=>{
        sessionStorage.setItem("customer_ids" , `[${res?.data?.success?.last_insert_id}]`)
    })
    .catch(err=>{
        // toast.error(err.response.data.message, {
        //     theme: "colored",position: 'top-center',
        // }); 
    })
}

//tracking status
export const track_status=(values , setData ,setLoading,setType, setMessage)=>{
    setLoading(true)
    axios.post(api_url+"api/gettrackingstatus" , {tracking_id:values} , { headers: { 'Authorization': 'Bearer ' + sessionStorage.getItem("token") } })
    .then(res=>{
        
        if(res?.data?.data[0])
        setData(res.data?.data[0])
        // sessionStorage.setItem("customer_ids" , res.data.success.last_insert_id )
        // toast.success("Quote stored successfully", {
        //     theme: "colored",position: 'top-center',
        // });
        setLoading(false) 
    })
    .catch(err=>{
        setLoading(false)
        setType("error")
        setMessage("Something went wrong")
    })
}

