import moment from 'moment'
import React from 'react'
import { Line } from 'react-chartjs-2'
function LineChart({ graphData }) {
    let dataSet = graphData.map(e => e.total)
    let months = graphData.map(e => moment(e.month, "MM").format("MMM"))
    console.log("data set", dataSet)
    return (
        <div>
            <Line
                data={{
                    // labels: months,
                    labels: ["jan", "feb ", "march", "april", "may" , "june"],
                    datasets: [{
                        lineTension: 0.5,
                        label: 'Pending Order Statistics',
                        // data: dataSet,
                        data: [0, 10, 5, 4, 8, 3],
                        backgroundColor: [
                            'rgba(255, 99, 132, 0.2)',
                            'rgba(54, 162, 235, 0.2)',
                            'rgba(255, 206, 86, 0.2)',
                            'rgba(75, 192, 192, 0.2)',
                            'rgba(153, 102, 255, 0.2)',
                            'rgba(255, 159, 64, 0.2)'
                        ],
                        borderColor: [
                            'rgba(255, 99, 132, 1)',
                            'rgba(54, 162, 235, 1)',
                            'rgba(255, 206, 86, 1)',
                            'rgba(75, 192, 192, 1)',
                            'rgba(153, 102, 255, 1)',
                            'rgba(255, 159, 64, 1)'
                        ],
                        borderWidth: 4
                    },
                    {
                        lineTension: 0.5,
                        label: 'Success Order Statistics',
                        // data: dataSet,
                        data: [0, 8, 5, 7, 4, 5],
                        backgroundColor: [
                            'rgba(255, 99, 132, 0.2)',
                            'rgba(54, 162, 235, 0.2)',
                            'rgba(255, 206, 86, 0.2)',
                            'rgba(75, 192, 192, 0.2)',
                            'rgba(153, 102, 255, 0.2)',
                            'rgba(255, 159, 64, 0.2)'
                        ],
                        borderColor: [
                            'rgba(255, 99, 132, 1)',
                            'rgba(54, 162, 235, 1)',
                            'rgba(255, 206, 86, 1)',
                            'rgba(75, 192, 192, 1)',
                            'rgba(153, 102, 255, 1)',
                            'rgba(255, 159, 64, 1)'
                        ],
                        borderWidth: 4
                    }
                ],
                    options: {
                        showTooltips: true,
                        scales: {
                            y: {
                                beginAtZero: true,
                                ticks: {
                                    stepSize: 2
                                }
                            }
                        },

                    }
                }}

            />
        </div>
    )
}

export default LineChart
