import React, { useEffect, useState } from "react";
import { Container, option, select } from "react-bootstrap";
import { Row, Col, DatePicker, InputNumber } from "antd";
import "../PaymentPage/UpdateInfo.css";
import { Form, Input, Button } from "antd";
import Credit from "../../assets/Credit-Card-Logos.png";
import "./PaymentInfo.css";
import moment from "moment";
import { useHistory } from "react-router";
import { update_card, update_card_debit_credit } from "../../api/updateQuote";
import { toast } from "react-toastify";
import Loader from "../../pages/Loader";
import { get_card_list } from "../../api/userUpdate";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import CreditCardInput from 'react-credit-card-input';
// import { user_registration } from "../../redux-services/user-services/user-services";

const PaymentSection = (props) => {

  const [loading, setLoading] = useState(false);
  const selector = useSelector(state => state.auth?.cardDetail)
  const quoteDetail = useSelector(state => state.auth?.quoteDetail)
  const dispatch = useDispatch()
  const history = useHistory()
  const onFinish = (values) => {
    Object.assign(values, { expiry_month: moment(values.expiry).format("MM") });
    Object.assign(values, {
      expiry_year: moment(values.expiry).format("YYYY"),
    });

    Object.assign(values, { card_type: "debite" });
    // user_registration(values);
    let val = values;
    delete val.expiry;
    if (!selector?.card_number) {
      // for adding new card  
      update_card(val, setLoading, dispatch, history, selector, quoteDetail ,props.setType, props.setMessage);
    } else {
      //for updating card
      Object.assign(val, { id: selector.id })
      update_card_debit_credit(val, setLoading, dispatch, history, selector ,props.setType, props.setMessage)
    }
  };

  const onFinishFailed = (errorInfo) => {
  };
  useEffect(() => {
    get_card_list(dispatch, setLoading, selector)
    // if (!selector?.card_number)
    //   toast.error("Please enter card detail to place order", {
    //     theme: "colored", position: 'top-center',
    //   });
  }, [])
  return (
    <div>
      <div>

        <div className="img pyment_logos">
          <img src={Credit} height="25" />
        </div>
        <Row>
          <Col md={24} lg={24} sm={24}>
            <Form
              className="mt-3"
              name="basic"
              initialValues={{
                remember: true,
              }}
              onFinish={onFinish}
              onFinishFailed={onFinishFailed}
              autoComplete="off"
              fields={
                [
                  {
                    name: "card_number",
                    value: selector?.card_number
                  },
                  {
                    name: "name_on_card",
                    value: selector?.name_on_card
                  },
                  {
                    name: "expiry",
                    value: selector?.expiry_year && (moment(`${selector.expiry_year}-${selector.expiry_month}`, 'YYYY-MM'))
                  }
                ]
              }
            >
              <Form.Item
                label="Card Number"
                name="card_number"
                rules={[
                  {
                    required: true,
                    max: 16,
                    min: 16,
                    message: "Card length should be 16 digit !",
                  },
                ]}
              >
                <Input placeholder="123412312341231" />
              </Form.Item>
              <Form.Item
                label="Name on card"
                name="name_on_card"
                rules={[
                  {
                    required: true,
                    message: "Please input your card name!",
                  },
                ]}
              >
                <Input placeholder="John Doe" />
              </Form.Item>
              <Form.Item
                label="Expiry/CVV"
                name="expiry"
                rules={[
                  {
                    required: true,
                    message: "Please input expiry date!",
                  },
                ]}
              >
                <DatePicker
                  picker="month"
                  defaultValue={moment("2022-12", "YYYY-MM")}
                />
              </Form.Item>
              <Form.Item
                name="card_cvv_number"
                rules={[
                  {
                    required: true,
                    max: 3,
                    min: 3,
                    // pattern: new RegExp("\d\d\d\d"),
                    message: "Please input cvv Number ",
                  },
                ]}
              >
                <Input.Password placeholder="CVV" />
              </Form.Item>
              <Form.Item
                wrapperCol={{
                  offset: 6,
                  span: 16,
                }}
              >
                <Button htmlType="submit" className="w-100 light-blue-bg">
                  {selector?.card_number ? "Update" : "Submit"}
                </Button>
              </Form.Item>
            </Form>
          </Col>
        </Row>
      </div>
      {
        loading && <Loader />
      }
    </div>
  );
};
export default PaymentSection;
